

















































import { Component, Prop, Vue } from 'vue-property-decorator';

@Component
export default class ProjectCard extends Vue {
  @Prop({ required: true })
  imageURI: string;

  @Prop({ required: true })
  projectName: string;

  @Prop({ required: true })
  projectDesc: string;

  @Prop({ required: true })
  projectTags: string;

  @Prop({ default: '' })
  projectURL: string;

  show: Boolean = false;
}
