









































import { Component, Vue } from 'vue-property-decorator';
import ParallaxItem from '@/components/portfolio/ParallaxItem.vue';
import ExperienceList from '@/components/portfolio/ExperienceList.vue';
import ProjectList from '@/components/portfolio/ProjectList.vue';
import AboutMe from '@/components/portfolio/AboutMe.vue';
import ContactForm from '@/components/portfolio/ContactForm.vue';
import { retrieveItem } from '@/storage/StorageHandler';
import { mapActions } from 'vuex';


@Component({
  components: {
    ParallaxItem,
    ExperienceList,
    ProjectList,
    AboutMe,
    ContactForm,
  },
})


export default class Portfolio extends Vue {
  sticky: boolean = false;

  stickyOffset: any = 0;

  offsets: number[] = [0, 0, 0, 0];

  scrollPos: number = 0;

  options() {
    return {
      duration: 300,
      offset: 0,
      easing: 'easeInOutCubic',
    };
  }

  get isIcon() {
    return (window.innerWidth <= 600);
  }

  mounted() {
    this.retrieveFromStorage();
    window.addEventListener('scroll', this.onScroll);
    this.stickyOffset = document.getElementById('navbar').offsetTop;
  }

  destroyed() {
    window.removeEventListener('scroll', this.onScroll);
  }

  get isDarkMode() {
    return this.$store.getters.isDarkMode;
  }

  toggleDarkMode(isDark: boolean) {
    const payload = {
      darkMode: isDark,
    };
    this.$store.dispatch('toggleDarkMode', payload);
  }

  retrieveFromStorage() {
    const isDark: boolean = retrieveItem('darkMode');
    this.toggleDarkMode(isDark);
  }

  onScroll() {
    this.sticky = (window.pageYOffset >= this.stickyOffset);
  }

  // Handled by Vuetify Library
  // scrollToSection(id: string){
  //   const target = document.getElementById(id);
  //   if(target == null) return;

  //   history.pushState({}, document.title, `#id`);
  //   target.scrollIntoView({
  //     behavior: 'smooth',
  //     block: 'start'
  //   });
  // }
}
